<template>
  <div class="order-content">
	<order-content-item v-for="(item,index) in orderType" :key="index" @click.native="itemClick(item.orderid)">
		<span class="order-num1" slot="order-num">{{item.ordercode}}</span><!--订单类型--->
		<span class="order-status" slot="order-status" :class="colors[item.status]">{{item.text}}</span><!--订单类型--->
		<span slot="order-type" class="order-type" :class="{caigoucolor:item.typetext=='销售订单'}">{{item.typetext}}</span><!--订单类型--->
		<!--订单里面每一行-->
		<item slot="item">
			<span class="item-span-title" slot="item-span-title">申请日期({{item.orderid}})：</span>
			<span class="item-span-content" slot="item-span-content">{{item.createtime}}</span>
		</item>
		<!--订单里面每一行-->
		<item slot="item">
			<span class="item-span-title" slot="item-span-title">要求到货日期：</span>
			<span class="item-span-content" slot="item-span-content">{{item.updatetime}}</span>
		</item>
		<!--订单里面每一行-->
		<item slot="item">
			<span class="item-span-title" slot="item-span-title" v-if="item.text=='待审核'">申请人：</span>
			<span class="item-span-title" slot="item-span-title" v-else>客户名称：</span>
			<span class="item-span-content" slot="item-span-content">{{item.name}}</span>
		</item>
		<div slot="detailinfo">
				<span class="item-span-title">{{item.username}}</span>
				<span class="item-span-content time">{{item.updatetime}}</span>
				<span class="item-span-detail">查看详情</span>
		</div>
	</order-content-item>
  </div>
</template>

<script>

import OrderContentItem from './OrderContentItem'
import Item from './Item'
  export default {
    name: "OrderContent",
	components:{
		OrderContentItem,
		Item
	},
	props:{
		OrderContentArr:{
			type:Array,
			default(){
				return[]
			}
		}
	},
	data(){
		return{
			colors:[
				'cgreen',
				'cred',
				'cblue',
			]
		}
	},
	methods:{
		itemClick(orderid){
			//跳页面
			this.$router.push("/detailorder/"+orderid).catch(err=>{err})
		}
	},
	//计算属性
	computed:{
		orderType(){
			return this.OrderContentArr
		},
	},
  }
</script>

