<template>
	<div class="top-banner-navs">
		<div class="customer" v-for="(item,index) in BannerNavsArr" :key="index" :class="{cactive:currentIndex==index}" @click="itemClick(index)">{{item}}</div>
	</div>
</template>

<script>



export default {
  name: 'TopBannerNavs',
  data(){
	return{
		currentIndex:0,
	}
  },
  props:{
	BannerNavsArr:{
		type:Array,
		default(){
			return []
		}
	}
  },
  components:{
  },
  methods:{
	itemClick(index){
		if(this.currentIndex!==index){
			this.currentIndex=index
			this.$emit("customerClick",this.currentIndex)
		}
		
	}
  }
}
</script>


<style>
.top-banner-navs{
	display:flex;
	background:#fff;
}

.customer{
	flex:1;
	text-align:center;
	line-height:45px;
	height:45px;
}
.cactive{
	border-bottom:3px solid #ff5959;
	color:#ff5959;
}

</style>
