//订单
export const itemListenerMixin={
	data(){
		return {
			currentType:'today'
		}
	},
	mounted(){
		//相同的东西可以抽离出来
		/*let newRefresh=debounce(this.$refs.scroller.refresh,100)
		
		this.$bus.$on("DetailImageLoad",()=>{
			this.$refs.scroller && this.$refs.scroller.finishPullUp();
			newRefresh()
		})*/
			//console.log("我是混入内容合并了miunted的方法")
	},
	created(){
		
		//获取入库getStockRoomList
		//今天-昨天-7天-三十天--这里问题是所有都共用了 抽离出来就行了
		//每次进入都清空一下数组就行了-暂时的方法
		this.clearStoreArr('getWarehouseMsg')
		this.$store.dispatch('getStockRoomList',{statusid:0,date:0,page:1,type:'getStockRoomList'});
		this.$store.dispatch('getStockRoomList',{statusid:0,date:1,page:1,type:'getStockRoomYestodayList'});
		this.$store.dispatch('getStockRoomList',{statusid:0,date:7,page:1,type:'getStockRoomServerList'});
		this.$store.dispatch('getStockRoomList',{statusid:0,date:30,page:1,type:'getStockRoomSanshiList'});
		
		
		//出库getDayShipmentList getShipmentList
		//每次进入都清空一下数组就行了-暂时的方法
		this.clearStoreArr('getShipmentMsg')
		this.$store.dispatch('getShipmentList',{statusid:1,date:0,page:1,type:'getShipmentList'});
		this.$store.dispatch('getShipmentList',{statusid:1,date:1,page:1,type:'getShipmentYestodayList'});
		this.$store.dispatch('getShipmentList',{statusid:1,date:7,page:1,type:'getShipmentServerList'});
		this.$store.dispatch('getShipmentList',{statusid:1,date:30,page:1,type:'getShipmentSanshiList'});
		
		
		
		
		//今天的订单-搞这里  这里还没有搞---今日销售 统计页面用了这里
		this.clearStoreArr('getOrderMsg')
		this.$store.dispatch('getDaySaleOrders',{typeid:0,page:0,date:0,type:'getDayOrderList'});
		this.$store.dispatch('getDaySaleOrders',{typeid:0,page:0,date:1,type:'getYestodayOrderList'});
		this.$store.dispatch('getDaySaleOrders',{typeid:0,page:0,date:7,type:'severDayOrderList'});
		this.$store.dispatch('getDaySaleOrders',{typeid:0,page:0,date:1000,type:'sanshiDayOrderList'});

		
		//------------------------------------------------------------------------------
		
		
		
		//采购getDayShipmentList-搞这里
		//每次进入都清空一下数组就行了-暂时的方法
		this.clearStoreArr('getPurchaseMsg')
		this.$store.dispatch('getDayPurchaseList',{id:1,page:1,date:0,type:'getTodayPurchaseList'});
		this.$store.dispatch('getDayPurchaseList',{id:3,page:1,date:20,type:'getYestodayPurchaseList'});
		this.$store.dispatch('getDayPurchaseList',{id:3,page:1,date:7,type:'getSeverDayPurchaseList'});
		this.$store.dispatch('getDayPurchaseList',{id:3,page:1,date:100,type:'getSssanShiDayPurchaseList'});
		//获取出库单getDayShipmentList
	},
	methods:{
		//今日 昨日点击按钮
		profitDateClick(index){
			//点击分类
			switch(index){
				case 0:
					this.currentType="today"
					break
				case 1:
					this.currentType="yestoday"
					break
				case 2:
					this.currentType="severday"
					break
				case 3:
					this.currentType="sanshiday"
					break
			}
		},
		//清空数组
		clearStoreArr(arrStr){
			this.$store.state[arrStr].today.Msg=[]
			this.$store.state[arrStr].yestoday.Msg=[]
			this.$store.state[arrStr].severday.Msg=[]
			this.$store.state[arrStr].sanshiday.Msg=[]
		}
	},
	//计算属性
	computed: {
		...mapGetters(['getOrderMsg','getWarehouseMsg','getPurchaseMsg','getShipmentMsg'])			//获取的时候使用this.getOrderMsg
    },
}

//客户
export const itemCustomerMixin={
	data(){
		return {
			//客户排行数组
			customerList:{
				customers:{
					countMoney:0,
					countNum:0,
					list:[],
					page:0,
					type:'客户',
					isMore:true,
					typeid:0,
				},
				supplier:{
					type:'供应商',
					list:[],
					page:0,
					isMore:true,
					typeid:1
				},
			},
			sortType:'productid',
			currentType:'customers',
		}
	},
	mounted(){
	},
	created(){
		//获取数据-先执行依稀-使用vuex获取
		//this.$store.dispatch('customerList',{page:1,type:'getCustomerList'});
		this.getCustomerList(0,'customers')			//客户
		this.getCustomerList(1,'supplier')			//供应商
	},
	methods:{
		//加载更多
		loadMove(){
			//防抖
			if(timer) clearTimeout(timer)
			let timer=setTimeout(()=>{
				if(this.customerList[this.currentType].isMore){
					this.getCustomerList(this.customerList[this.currentType].typeid,this.currentType)
					this.$refs.scroller.finishPullUp()
				}
					this.$refs.scroller.refresh()
			},80)
			//有问题-条数少了有问题
		},
		//获取客户数据
		getCustomerList(typeid,type){
			let page=this.customerList[type].page+1;
			getCustomerList(typeid,page).then(res=>{
				//console.log(res)
				this.customerList[type].list.push(...res.customerList);
				this.customerList[type].page+=1			//如果没有数据了
				this.customerList[type].countMoney=res.countMoney
				this.customerList[type].countNum=res.countNum
				if(res.customerList.length<=0){
					//不在加载更多
					this.customerList[type].isMore=false
				}
			})
		},
		//排序-需要抽到工具类
		compare (property){
			return function(a,b){
				let value1=a[property]
				let value2=b[property]
				return value2-value1
			}
		},
	},
	//计算属性
	computed: {
		...mapGetters([]),				//获取的时候使用this.getOrderMsg,
		customerLists(){
			return this.customerList[this.currentType].list
		}
    },
	watch:{
		//有新数据都刷新一下
		customerLists(){
			this.$nextTick(() => {
				this.$refs.scroller.refresh()
			})
		}
	}
}


//货品

//订单详情
export const itemOrderDetailMixin={
	data(){
		return{
			orderDetailInfo:{
				id:0,
				name:'',
				sex:0,
				phone:0,
				telephone:0,
				locat:0,
				createtime:0,
				updatetime:0,
				ordercode:'YX00000000',
				amount:0,
				handlerid:0,
				amountpaid:0,
				productInfo:[],		//产品列表
			},
		}
	},
	//方法
	methods:{
		getOrderDetail(id){
			getOrderDetail(id).then(res=>{
				this.orderDetailInfo.productInfo.push(...res.product)
				this.orderDetailInfo.id=res.id							//id
				this.orderDetailInfo.name=res.name						//客户名
				this.orderDetailInfo.sex=res.sex						//性别
				this.orderDetailInfo.phone=res.phone					//手机
				this.orderDetailInfo.amount=res.amount					//手机
				this.orderDetailInfo.handlerid=res.handlerid			//下单人
				this.orderDetailInfo.amountpaid=res.amountpaid			//手机
				this.orderDetailInfo.telephone=res.telephone			//固话
				this.orderDetailInfo.ordercode=res.ordercode			//固话
				this.orderDetailInfo.locat=res.location					//地址
				this.orderDetailInfo.createtime=res.createtime			//创建时间
				this.orderDetailInfo.updatetime=res.updatetime			//修改时间
			})
		}
	},
	//计算属性
	computed:{
		detailInfo(){
			return this.orderDetailInfo
		},
		productList(){
			return this.orderDetailInfo.productInfo
		}
	},
	created(){
	},
	//处于活跃状态
	activated(){
		this.orderDetailInfo.productInfo=[];			//进入的时候清空数组一下
		this.getOrderDetail(this.$route.params.id)	
	},
	//加载完数据刷新一下
	watch:{
		productList(){
			this.$nextTick(() => {
				this.$refs.scroller.refresh()
			})
		}
	}
}
