//获取历史订单和单据
export const itemHistoryOrdersMixin={
	data(){
		return{
			topBannerNav:[
				'全部','待审核','待执行','已完成'
			],
			HistoryInfo:{
				allOrders:{				//全部
					page:0,
					list:[],
					isMore:true,
					statusid:3,
				},
				waitAuditing :{			//待审核
					page:0,
					list:[],
					isMore:true,
					statusid:1,
				},
				waitExecute:{			//待执行
					page:0,
					list:[],
					isMore:true,
					statusid:2,
				},
				successOrders:{			//已完成
					page:0,
					list:[],
					isMore:true,
					statusid:0,
				},
			},
			currentType:'allOrders',		//当前选中的类别
			
		}//return结尾
	},
	methods:{
		getHistoryOrders(typeid,type){
			let page=this.HistoryInfo[type].page+=1
			getHistoryOrders(typeid,page).then(res=>{
				//console.log(res)
				this.HistoryInfo[type].list.push(...res)
				this.HistoryInfo[type].page+=1
				if(res.length<=0){
					//不在加载更多
					this.HistoryInfo[type].isMore=false
				}
			})
		},
		//客户和供应商的点击
		customerClick(index){
			switch(index){
				case 0:
					this.currentType="allOrders"
					break
				case 1:
					this.currentType="waitAuditing"
					break
				case 2:
					this.currentType="waitExecute"
					break
				case 3:
					this.currentType="successOrders"
					break
			}		
				//console.log(this.currentType)	
		},
		//返回按钮
		backClick(){
			this.$router.go(-1)
		},
		loadMove(){
			//防抖
			/*if(timer) clearTimeout(timer)
			let timer=setTimeout(()=>{
				this.getHistoryOrders(this.HistoryInfo[this.currentType].statusid,this.currentType)
				if(this.HistoryInfo[this.currentType].isMore){
					this.$refs.scroller.finishPullUp()
				}
					this.$refs.scroller.refresh()
			},50)*/
			//有问题-条数少了有问题
			console.log("加载更多")
		}
	},
	//计算属性
	computed:{
		orderList(){
			return this.HistoryInfo[this.currentType].list
		}
	},
	created(){
		
	},
	activated(){
		this.HistoryInfo.allOrders.list=[]
		this.HistoryInfo.waitAuditing.list=[]
		this.HistoryInfo.waitExecute.list=[]
		this.HistoryInfo.successOrders.list=[]
		this.getHistoryOrders(3,'allOrders')				//0：全部
		this.getHistoryOrders(1,'waitAuditing')				//1：待审核
		this.getHistoryOrders(2,'waitExecute')				//2：待执行
		this.getHistoryOrders(0,'successOrders')			//3：已完成
		//console.log(this.HistoryInfo)
	}
}


//客户详情
export const itemCustomerDetailMixin={
	data(){
		return{
			//当前选中的列表(所有单据)
			currentType:'allDocuments',
			customerInfo:{
				name:'',
				id:0,
				allDocuments:{
					page:0,
					list:[],
					isMore:true,
				},
				arrearsDocuments:{
					page:0,
					list:[],
					isMore:true,
				},
				collectDocuments:{
					page:0,
					list:[],
					isMore:true,
				},
			},
		}
	},
	methods:{
		backClick(){
			this.$router.go(-1)
		},
		loadMove(){
			
		},
		//获取总数
		getAllAttrNum(value){
			let res=value;
			return res
		},
		//头部三个菜单点击切换
		receiptsNavsClick(index){
			switch(index){
				case 0:
					this.currentType="allDocuments"
					break	
				case 1:
					this.currentType="arrearsDocuments"
					break	
				case 2:
					this.currentType="collectDocuments"
					break	
			}
		},
		//获取客户详情
		getCustomerDetail(type){
			let page=this.customerInfo[type].page+=1					//页码
			let cid=this.$route.params.id								//客户的id
			getCustomerDetail(cid,page,type).then(res=>{
				//console.log(res)
				this.customerInfo[type].list=res.orders					//订单列表
				this.customerInfo.name=res.name							//名字
				this.customerInfo.id=res.id								//编号
				this.customerInfo[type].page+=1							//页码
			})
		},
		//返回总值
		MatchAllNum(arr,key){
			let res=0
			if(!arr || arr.length==0)return res
			arr.forEach(obj=>{
				res+=parseFloat(obj[key])
			})
			return res
		}
	},
	//计算属性
	computed:{
		//菜单栏显示
		/*MakeCollInfos(){
			//获取
			let yingshou=this.getAllAttrNum(1)
			let shishou=this.getAllAttrNum(2)
			//设置
			this.setValue('yingshou',yingshou)
			this.setValue('shishou',shishou)
			this.setValue('arrears',yingshou-shishou)
			return this.MakeCollectionsInfos
		},*/
		customerOrders(){
			return this.customerInfo[this.currentType].list
		},
		shouKuanInfos(){
			let res=this.MatchAllNum(this.customerInfo[this.currentType].list,'amount')
			return{yingshou:res,shishou:50,arrears:res-50}
		},
		MakeCollInfos(){
			let res=this.MatchAllNum(this.customerInfo[this.currentType].list,'amount')
			return{yingfu:res,shifu:30,arrears:res-50}
		},
	},
	created(){
	},
	activated(){
		this.getCustomerDetail('allDocuments')			//1:allDocuments全部单据
		this.getCustomerDetail('arrearsDocuments')		//1:arrearsDocuments欠款单据
		this.getCustomerDetail('collectDocuments')		//1:collectDocuments收款单据
	},
	deactivated(){
		this.customerInfo[this.currentType].page=0
	},
	watch:{
		customerOrders(){
			this.$nextTick(() => {
				this.$refs.scroller.refresh()
			})
		}
	}
}
